import React, { memo, useState,  useCallback, useMemo} from "react";
import { useDispatch } from "react-redux";
import clsx from 'clsx'
import {useDropzone} from 'react-dropzone'
import Typography from "@mui/material/Typography";
import { styled } from "@mui/material";
import Paper from '@mui/material/Paper';
import grey from '@mui/material/colors/grey';
import Box from '@mui/material/Box';

const StyledPaper = styled(Paper)(
    ({theme}) => ({
        height: 350,
        width: 550,
        borderWidth: 2,
        borderRadius: 10,
        borderStyle: 'dashed',
        borderColor: theme.palette.secondary.main,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: grey[200],
    })
)
const StyledTypography = styled(Typography)(
    ({theme}) => ({
        color: theme.palette.text.secondary,
        fontWeight: '500',
        fontSize: theme.spacing(2.2),
        fontStyle: 'italic'
    })
);

const FileInput = ({data, name, onChange, placeholder, disabled}) => {
    const dispatch = useDispatch();
    const onDrop = useCallback(acceptedFiles => {
        if(acceptedFiles.length > 0 ){
            const file =  acceptedFiles[0]
            onChange(file)
        }
    }, [onChange])
    const {getRootProps, getInputProps} = useDropzone({onDrop});

    const RootElement = useMemo(()=>{
        if(!!data){
            return (
                <Box className={clsx('full-width')} sx={{paddingLeft:1, paddingRight: 1}} >
                    <StyledTypography>
                        {placeholder}
                    </StyledTypography>
                    <StyledTypography 
                        sx={{
                            color: theme => theme.palette.secondary.main,
                            paddingTop: theme => theme.spacing(1),
                            fontWeight: 'bold'
                        }}
                        className={clsx('ellipsis')}
                    >
                        {name}
                    </StyledTypography>
                </Box>
            )
        }else{
            return (
                <StyledTypography>
                   {placeholder}
                </StyledTypography>
            )
        }
    }, [data])

    return (
        <div style={{textAlign: 'center'}}>
            <Box>
                <StyledPaper elevation={0} {...getRootProps()} >
                    <input disabled={disabled} {...getInputProps()} />
                    { RootElement }
                </StyledPaper>
            </Box>
        </div>
        
    )
}
FileInput.defaultProps = {
    data: '',
    onChange: () => {},
    placeholder: 'Drop New Image',
    loading: false,
    disabled: false
}
export default memo(FileInput)