import React, { memo, useState, useCallback } from "react";
import { useDispatch } from "react-redux";
import clsx from "clsx";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { setUploading } from "../modules/help";
import MainLayout from "../layouts/MainLayout";
import FileInput from "../components/inputs/FileInput";

import * as FileAPI from "../services/api";
import * as IdbAPI from "../services/idb";

import { setGlobalMessage, setFcs, setCurrentFc, setCurrentResult } from "../modules/help";
import { getFcs } from "../utils/calulator";

const getStatistics = async (fc) => {
    try {
        const statisticsData = await FileAPI.getResult(fc, null, null);
        if(statisticsData['data']['status']) {
            const result = statisticsData['data']['result']
            return result;
        }
    }catch(error) {
        return null;
    }
}

const UpdateDB = () => {
    const [value, setValue] = useState({
        file: null,
        name: ''
    })
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const onChangeFile = useCallback((file) => {
        if(!!file) setValue(value => ({ ...value, file: file, name: file['name']}))
    }, []);
    
    const onUpload = useCallback(async () => {
        dispatch(setUploading('Uploading...'));
        const {file} = value;
        try{
            if(!!file) {
                const {data} = await FileAPI.uploadDB(file);
                let {error, locations} = data;
                dispatch(setUploading(null));
                if(!!error) dispatch(setGlobalMessage({ status: false,  message: error }));
                else {
                    const fcs = getFcs(locations);
                    dispatch(setFcs(fcs));
                    if(!!fcs || !!fcs.length) {
                        let currentFc = fcs[0];
                        dispatch(setCurrentFc(currentFc));
                        const result = await getStatistics(currentFc);
                        if(!!result) dispatch(setCurrentResult(result))
                    }
                    setValue(value => ({ ...value, name: '', file: null }));
                    dispatch(setGlobalMessage({ status: true, message: 'Database has been updated!' }));
                    navigate('/')
                }
            }
        }catch(error) {
            dispatch(setUploading(null));
            dispatch(setGlobalMessage({ status: false, message: error.message }))
        }
    }, [value])
    return (
        <>
            <MainLayout>
                <Box className={clsx('flex', 'justify-center', 'flex-grow')}>
                    <Box>
                        <FileInput 
                            name={value['name']}
                            data={value['file']}
                            placeholder="Drop New Database"
                            onChange={onChangeFile}
                        />
                        <Button 
                            variant="contained" 
                            disabled={!value['file']} 
                            sx={{width: '100%', mt: 3}}
                            onClick={onUpload}
                        >
                            Update 
                        </Button>
                    </Box>
                </Box>
            </MainLayout>
        </>
    )
}
export default memo(UpdateDB);